body {
    margin: 0;
    background-color: #FAFAFA;
    width: 100vw;
    height: 100%;
    position: relative;
}

#app {
    font-family: Noto Sans CJK TC, Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: rgba(0, 0, 0, 0.8);
}

div {
    box-sizing: border-box;
}

#nav {
    padding: 30px;
}

#nav a {
    font-weight: bold;
    color: #2c3e50;
}

#nav a.router-link-exact-active {
    color: #42b983;
}

.toast-popup {
    border: 1px solid #FD2E30!important;
    height: 56px;
}

.toast-close-btn {
    color: #FD2E30!important;
    width: 24px!important;
    height: 24px!important;
    font-size: unset!important;
}

.toast-title {
    font-style: normal;
    font-weight: normal!important;
    font-size: 14px!important;
    line-height: 21px;
    letter-spacing: 0.1px;
    color: #FD2E30!important;
}

.success-toast-popup {
    border: 1px solid #A2E5AA!important;
    height: 56px;
}

.success-toast-close-btn {
    color: #24C73A!important;
    width: 24px!important;
    height: 24px!important;
    font-size: unset!important;
}

.success-toast-title {
    font-style: normal;
    font-weight: normal!important;
    font-size: 14px!important;
    line-height: 21px;
    letter-spacing: 0.1px;
    color: #24C73A!important;
}

.modal-container {
    padding: 0!important;
}

.modal-popup {
    padding: 24px 16px!important;
}

.modal-title {
    font-style: normal;
    font-weight: 500!important;
    font-size: 18px!important;
    text-align: center;
    letter-spacing: 0.5px!important;
    color: rgba(0, 0, 0, 0.8) !important;
}

.modal-content {
    font-weight: 300!important;
    font-size: 16px!important;
    padding: 0!important;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.5px!important;
    color: rgba(0, 0, 0, 0.8) !important;
}

.modal-confirm-button {
    width: 240px;
    height: 46px;
    background: #FCA311!important;
    border-radius: 4px!important;
    font-weight: 500!important;
    font-size: 16px!important;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.5px;
    color: #FFFFFF!important;
}

.modal-confirm-button-half {
    width: 114px;
