}

.modal-confirm-button-130 {
    width: 130px;
    height: 46px;
    background: #FCA311!important;
    border-radius: 6px!important;
    font-weight: 500!important;
    font-size: 16px!important;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.5px;
    color: #FFFFFF!important;
    padding-right: 24px!important;
