<template>
  <div id="app">
<!--    <div id="nav">-->
<!--      <router-link to="/">Home</router-link> |-->
<!--      <router-link to="/about">About</router-link>-->
<!--    </div>-->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- Cache View -->
      </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
      <!-- No cache view -->
    </router-view>
    <div style="position: absolute; top: 0; width: 100vw; height: 100vh; background: white;
                display: flex; flex-direction: column; justify-content: center; align-items: center"
                v-if="showInfo">
      <div style="width: 320px; text-align: left">Notification status :</div>
      <div style="width: 320px; text-align: left">{{status}}</div>
      <br>
      <div style="width: 320px; text-align: left">Firebase messaging :</div>
      <div style="width: 320px; text-align: left">{{support}}</div>
      <br>
      <div style="width: 320px; text-align: left">firebase token :</div>
      <div style="width: 320px; text-align: left">{{token}}</div>
      <br>
      <div style="width: 320px; text-align: left">error msg :</div>
      <div style="width: 320px; text-align: left">{{err}}</div>

      <div @click="onConfirm" class="info-button-test">OK</div>
    </div>
    <div style="position: absolute; top: 0; width: 100vw; height: 100vh; background: rgba(0, 0, 0, 0.3);
                display: flex; justify-content: center; align-items: center"
         v-if="showInstall && !appInstalled">
      <div style="background: #FFFFFF; width: 245px; padding: 16px; border-radius: 12px;
                      text-align: left; color: rgba(0, 0, 0, 0.8)">
        <div style="font-size: 18px; font-weight: bold; margin-bottom: 6px">
          加入主畫面
        </div>
        <div style="font-size: 14px; margin-bottom: 14px">
          為了更好的使用體驗，請點選「確認」，將此應用程式加到主畫面
        </div>
        <div style="display: flex; justify-content: flex-end">
          <div style="color: #2F80ED; font-size: 16px; text-align: center; margin-right: 10px" @click="cancel">取消</div>
          <div style="color: #2F80ED; font-size: 16px; text-align: center " @click="install">確認</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapActions } from 'vuex';
  import firebase from 'firebase/app';

  export default {
    components: {},
    data() {
      return {
        showBlock: false,
        showInstall: false,
        appInstalled: false,
        showInfo: true,
        deferredPrompt: '',
        token: 'none',
        err: 'none',
        status: 'none',
        support: 'none',
      };
    },
    created() {
      // eslint-disable-next-line
      try {
        this.support = firebase.messaging.isSupported();
      this.$messaging.getToken({ vapidKey: 'BBfrcboJrsdfhanxzx911pUgadG30bKf392lxjilLJ49_1bndzIAO53EvHJLcRDC-xIg_Sv2ofViIDHNLgjkEP8' })
        .then((token) => {
          this.saveMessageToken(token);
          this.token = token;
          console.log(token);
        })
        .catch((err) => {
          this.err = JSON.stringify(err);
        });

      this.$messaging.onMessage((payload) => {
        console.log(payload);
        console.log(Notification.permission);
        if (Notification.permission === 'granted') {
          navigator.serviceWorker.ready.then((registration) => {
            registration.showNotification(payload.notification.NotifSubject, {
              body: payload.notification.NotifContent,
            });
          });
        }
      });

      window.onappinstalled = (event) => {
        this.appInstalled = true;
      };

      // alert(`isInStandaloneMode: ${this.isInStandaloneMode}, isNotification: ${this.isNotification}`);
      if (this.isInStandaloneMode) {
        Notification.requestPermission((status) => {
          this.status = status;
          console.log('Notification permission status:', status);
        });
      }

      this.saveDeviceInfo(this.isIos);

      window.addEventListener('beforeinstallprompt', (e) => {
        if (this.isIos) return;
        // Prevent the mini-infobar from appearing on mobile
        e.preventDefault();
        // Stash the event so it can be triggered later.
        this.deferredPrompt = e;
        // Update UI notify the user they can install the PWA
        this.showInstall = true;
        // this.showInstallHandler();
      });
      } catch (err) {
        this.err = err;
      }
    },
    mounted() {
      this.status = Notification.permission;
      this.showNotification();
    },
    computed: {
      isIos: () => {
        const userAgent = window.navigator.userAgent.toLowerCase();
        return /iphone|ipad|ipod/.test(userAgent);
      },
      isInStandaloneMode: () => (('standalone' in window.navigator) && (window.navigator.standalone))
        || (window.matchMedia('(display-mode: standalone)').matches),
      isNotification: () => ('Notification' in window),
    },
    methods: {
      ...mapActions([
        'saveMessageToken',
        'saveDeviceInfo',
      ]),
      install() {
        this.deferredPrompt.prompt();
      },
      cancel() {
        this.showInstall = false;
      },
      onConfirm() {
        this.showInfo = false;
      },
      showNotification() {
        Notification.requestPermission((result) => {
          if (result === 'granted') {
            navigator.serviceWorker.ready.then((registration) => {
              console.log(registration);
              registration.showNotification('Vibration Sample', {
                body: 'Buzz! Buzz!',
              });
            });
          }
        });
      },
    },
  };
</script>

