/*隱藏SchemaDIV*/
.boxhide{ display:none;}
.boxhide p{ margin:0; padding:0;}

/*===================考試介紹共用區===================*/
/*內文文字樣式*/
.fs01{ margin:0; padding:5px 0; font-size:1em; color:#333; line-height:1.6;}/*註釋備註用*/
.fs01 a{ color:#0070c0; text-decoration:none;}
.fs01 a:hover{ color:#0070c0; text-decoration:underline;}
.fs02{ margin:0; padding:8px 0; font-size:1.1em; color:#0070c0; line-height:1.6; border-bottom:1px dashed #CCCCCC;}/*行銷導引連結*/
.fs02 a{ color:#f2511e; text-decoration:none;}
.fs02 a:hover{ color:#0070c0; text-decoration:underline;}
.fs03{ margin:0; padding:0 0 5px 0; font-size:1.1em; color:#333; line-height:1.8;}/*主要內文*/
.fs04{ margin:0; padding:25px 0 10px 0; font-size:1.2em; color:#333; font-weight:bold;}/*次標題*/
.fs05{ margin:0; padding:0; font-size:1.1em; color:#00aaaa; line-height:2;}/*重點標題*/
.fs06{ margin:0; padding:0; font-size:1em; color:#06F; line-height:2;}/*重點色*/
.fs07{ margin:0; padding:0; font-size:1em; color:#0070c0; line-height:2;}/*重點色*/


/*基本雙欄表格01*/
.table01{ display:table; width:100%;
        border-top:1px solid #a6a6a6;
		border-left:1px solid #a6a6a6;
		overflow:hidden;}
.table01 ul{ display:table-row; width:100%;
		   }
.table01 li{ display:table-cell;
           border-bottom:1px solid #a6a6a6;
		   border-right:1px solid #a6a6a6;
		   padding:5px;
		   vertical-align:middle;
		   font-size:1em;
		   line-height:1.6;
		   }
.table01 li:nth-child(odd){ width:25%; background-color:#f2f2f2; text-align:center; font-weight:bold;}
.table01 li:nth-child(even){ width:75%;}
.table01 li a{ text-decoration:none; color:#0070c0;}
.table01 li a:hover{ text-decoration:underline;}
.table01 p{ margin:0; padding:0;}

/*表格樣式02*/
.table02{ display:table; width:100%; margin:15px 0;
        border-top:1px solid #a6a6a6;
		border-left:1px solid #a6a6a6;
		overflow:hidden;}
.table02 ul{ display:table-row; width:100%;
		   }
.table02 ul:nth-child(1){ background-color:#f2f2f2; font-weight:bold;}
.table02 li{ display:table-cell;
           border-bottom:1px solid #a6a6a6;
		   border-right:1px solid #a6a6a6;
		   padding:5px;
		   vertical-align:middle;
		   font-size:1em;
		   line-height:1.6;
		   text-align:center;
		   }
.table02 p{ margin:0; padding:0;}

/*表格樣式03*/
.table03{ border-collapse: collapse; width:100%; margin:20px 0;}
.table03 td{border:1px solid #a6a6a6; border-collapse:collapse; padding:4px; text-align:center; font-size:14px;}

/*表格樣式04*/
.table04{ border-collapse: collapse; width:100%; margin:10px 0 20px 0;}
.table04 td{border:1px solid #a6a6a6; border-collapse:collapse; padding:6px 4px; font-size:15px;}
.table04 td a{ color:#000; font-weight:bold;}
.table04 td a:hover{ text-decoration:none;}

/*表格樣式05*/
.table05{ display:table; width:100%; margin:20px 0;
        border-top:1px solid #afcaca;
		border-left:1px solid #afcaca;
		overflow:hidden;}
.table05 dl{ margin:0; padding:0; width:100%;}
.table05 dt{ margin:0; padding:10px; font-size:16px; line-height:1.6;
             background-color:#f3fafa;
			 border-bottom:1px solid #afcaca;
		     border-right:1px solid #afcaca;
			 font-weight:bold;
			 color:#00a1a1;
			 }
.table05 dd{ width:100%; 
             border-bottom:1px solid #afcaca;
		     border-right:1px solid #afcaca;
		     padding:10px;
		     font-size:1em; line-height:1.6; font-weight:bold;
			 }
.table05 dd a{ color:#333;}
.table05 dd a:hover{ text-decoration:none;}
.table05 p{ margin:0; padding:0;}


/*===================表單樣式共用區===================*/
.form input,select,textarea{ margin:0; padding:0; outline:none; background-color:rgba(204,204,204,.1);}
.form input,textarea{ border-radius:0 0px 0px 0;}
.form .formstyle01{  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); tap-highlight-color: rgba(0, 0, 0, 0); appearance:none; -webkit-appearance:none; padding:5px; width:100%; height:35px; border:1px solid #CCC; font-size:14px; color:#999;}
.form .formstyle01-a{  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); tap-highlight-color: rgba(0, 0, 0, 0); appearance:none; -webkit-appearance:none; padding:5px; width:100%; height:auto; border:1px solid #CCC; font-size:14px; color:#999;}
.form .formstyle02{ width:100%; height:35px; border:1px solid #CCC; font-size:14px; color:#666;}
.form .formstyle03{ margin:0 0 10px 0; width:50%; height:35px; border:1px solid #CCC; font-size:14px; color:#666; float:left;}
.form .sumbit01{ appearance:none; -webkit-appearance:none; padding:10px 0; width:100%; background-color:#666; text-align:center; font-size:1.1em; color:#FFF; border:none; }
.form .sumbit02{ appearance:none; -webkit-appearance:none; padding:10px 0; width:100%; background-color:#c80000; text-align:center; font-size:1.2em; font-weight:bold; color:#FFF; border:none;}
.form .co{ color:#00ffd8;}

.form{ width:100%; margin:50px 0; padding:30px 0 0 0; overflow: hidden; border-top:1px solid #CCC;}
.form .form-box1{ width:100%; margin:0; font-size:1em;}
.form .form-box1 h3{ margin:0; padding:0; font-size:1.5em; color:#c80000;}
.form .form-box1 p{ margin:0; padding:10px 0; font-size:1em; color:#333;}

.form .form-box2{ width:100%; margin:0; padding:0; background-color:#fff; font-size:14px; line-height:2; color:#333;}
.form .form-box2-01{ width:49%; height:35px; margin:0 1% 10px 0; float:left; position:relative;}
.form .form-box2-01::before{ content:"";
                   display:block;
				   width:1px;
				   height:35px;
                   border-left:3px solid #F00;
				   position:absolute;
				   top:0;
				   left:0;
				   z-index:200;}
.form .form-box2-02{ clear:both; width:99%; height:35px; margin:0 0 10px 0; position:relative;}
.form .form-box2-02::before{ content:"";
                   display:block;
				   width:1px;
				   height:35px;
                   border-left:3px solid #F00;
				   position:absolute;
				   top:0;
				   left:0;
				   z-index:200;}
.form .form-box2-03{ clear:both; width:99%;}
.form .form-box2-03 p{ color: #09F;}
.form .form-box2-03-style01{  margin:0 0 10px 0; width:100%; height:35px; float:left; position:relative;}
.form .form-box2-03-style01::before{ content:"";
                   display:block;
				   width:1px;
				   height:35px;
                   border-left:3px solid #F00;
				   position:absolute;
				   top:0;
				   left:0;
				   z-index:200;}
.form .form-box2-03-style02{  margin:0 0 10px 0; width:100%; height:35px; float:left; position:relative;}
.form .form-box2-03-style02::before{ content:"";
                   display:block;
				   width:1px;
				   height:35px;
                   border-left:3px solid #F00;
				   position:absolute;
				   top:0;
				   left:0;
				   z-index:200;}

.form .form-box2-04{ clear:both; margin:0 0 10px 0; width:99%; height:35px; position:relative; display:inline-block;}
.form .form-box2-04::before{ content:"";
                   display:block;
				   width:1px;
				   height:35px;
                   border-left:3px solid #F00;
				   position:absolute;
				   top:0;
				   left:0;
				   z-index:200;}
				   
.form .form-box2-05{ margin:0 0 10px 0; width:99%; height:45px; position:relative; display:inline-block;}
.form .form-box2-05::before{ content:"";
                   display:block;
				   width:1px;
				   height:45px;
                   border-left:3px solid #F00;
				   position:absolute;
				   top:0;
				   left:0;
				   z-index:200;}

.form .B-sent{ margin:10px auto; padding:0; width:30%;}
.form p{ margin:0; padding:0;}




